import React from 'react';
import styled from 'react-emotion';
import { position } from 'polished';
import { palette } from 'css/variables';
import { gradientBg } from 'css/utilities';

const BgContour = styled('span')`
    ${position('fixed', 0)}
    z-index: 0;
    width: 100%;
    height: 100%;
    display: block;
    background-color: black;
    background-image: radial-gradient(circle at center 90%, rgba(22,24,23,1) 47%, rgba(0,0,0,0) 200%);
    
`;

const BgGradient = styled('span')`
    display: block;
    background-color: ${palette.greenDarkestAlt};
    ${position('fixed', 0)};
    ${gradientBg};
    background-blend-mode: darken;
`;

export default () => (
    <React.Fragment>
    <BgContour />
        <BgGradient />
    </React.Fragment>    
)




