import { injectGlobal } from 'react-emotion';
import { normalize } from 'polished';

injectGlobal`
    ${normalize()}
    html,
    body {
        height: 100%;
        min-height: 100%;
        overflow: hidden;
    }

    #root,
    .App {
        height: inherit;
        min-height: inherit;
    }
`



